type SortType = 'Rank' | 'Carmaker' | 'Revenue' | 'origin';

export default (({currency, gdp}) => {

    

    return (
        <CountryManufacturerTableContainer>
            <div className="scroller">
                <Table>
                    <TableHeaders>
                        <TableRow>
                            <th>Manufacturer</th>
                            <th>Revenue <span>and difference</span></th>
                            <th>Difference</th>
                        </TableRow>
                    </TableHeaders>

                    <TableBody>
                        {

                            companyData.map((company: Company, index: number) => {

                                const revenue: number = +company[`Revenue (${currency === Currency.GBP ? '£' : '$'})`].replace(/,/g, '');
                                const difference: number = gdp - revenue;
                                const differencePercentage: number = +relDiff(revenue, gdp).toFixed(0);
                                const currencySymbol: '£' | '$' = currency === Currency.GBP ? '£' : '$';
                                const differenceColour: '#DA1C5C' | '#29DE8E' = difference > 0 ? '#DA1C5C' : '#29DE8E';

                                const revenueLength = revenue.toLocaleString().split(',').length;
                                const revenueSuffix = {2: 'K', 3: 'M',4: 'B'}[revenueLength]

                                const shouldCutOffName = company.Carmaker.split(' ').shift().length > 10;

                                return (
                                    <TableRow key={`tr-for=${company.Carmaker}`}>
                                        <td>
                                            <div style={shouldCutOffName ? {maxWidth: '135px', overflow: 'hidden'} : {}}>
                                                <img src={require(`../images/logos/${company.Carmaker}.svg`)} alt={`logo-for-${company.Carmaker}`} /> 
                                                <span>{company.Carmaker}</span>
                                            </div>
                                        </td>
                                        <td>
                                            <div>
                                                <span className="mobile">{currencySymbol}{revenue.toLocaleString().split(',').shift()}{revenueSuffix}</span>
                                                <span className="desktop">{currencySymbol}{revenue.toLocaleString()}</span>
                                                <span style={{color: differenceColour}} className="difference">({Math.abs(differencePercentage).toLocaleString()}%)</span>
                                            </div>
                                        </td>   
                                        {/* {difference < 0 ? '-' :  '+'} */}
                                        <td style={{color: differenceColour}}>{currencySymbol}{difference.toLocaleString().replace('-', '')} ({Math.abs(differencePercentage).toLocaleString()}%)</td>
                                    </TableRow>
                                );

                            })

                        }
                    </TableBody>
                </Table>
            </div>
        </CountryManufacturerTableContainer>
    )
}) as CountryManufacturerComponent

interface props {
    currency: Currency, 
    gdp: number
};

type CountryManufacturerComponent = React.FC<props>;


function relDiff(oldNumber: number, newNumber: number){
    var decreaseValue = oldNumber - newNumber;

    return (decreaseValue / oldNumber) * 100;
}