import * as React from 'react';
import styled from 'styled-components';
import Footer from '../Components/Footer';
import Header from '../Components/Header';
import Map from '../Components/Map';
import Sidebar from '../Components/Sidebar';
import { Countries, Country, StateHook } from '../global.types';
import L from 'leaflet';
import useWindowSize from '../utils/useWindowSize';
import Lightbox from '../Components/Lightbox';
import CountryPopup from '../Components/CountryPopup';
import CurrencyToggler from '../Components/CurrencyToggler';

import Instruction from '../Components/Instruction';
import CountryData from '../Components/CountryData';
import ManufacturerData from '../Components/ManufacturerData';

const countries: Countries = require('../data/countryData.json');



// import Leaflet from 'leaflet';


// MAIN BREAKS AT 520 PX WIDTH

const App = styled.div<{currency: Currency, screenHeight: string}>`
    display: flex;
    height: ${({screenHeight}) => screenHeight};
    max-height: ${({screenHeight}) => screenHeight};
    max-width: 100vw;
    overflow: hidden;
    margin: 0 auto;
    max-width: 1380px;
    opacity: 0;
    animation: fadein 2s ease forwards;
    .currencyColour {
        color: ${({currency}) => currency};
        fill: ${({currency}) => currency};
        stroke: ${({currency}) => currency};
        &--hover {
            &:hover {
                stroke: ${({currency}) => currency};
                color: ${({currency}) => currency};
                fill: ${({currency}) => currency};
            }
        }
    }

    .currencyBackground {
        background: ${({currency}) => currency};
        fill: ${({currency}) => currency};
        &--hover {
            &:hover {
                fill: ${({currency}) => currency};
                background: ${({currency}) => currency} !important;
            }
        }
    }

    main {
        width: 100%;
        padding: 35px 35px;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    @media screen and (max-width: 520px) {
        main {
            padding: 20px 15px;
        }
    }

    @media screen and (max-width: 365px) {
        main {
            padding: 20px 12px;
        }
    }

`;


const Disclaimer = styled.p`
    color: #CACEE6;
    text-align: center;
    font-size: 8px;
    font-family: roc-grotesk-wide, sans-serif;
    font-weight: 400;
    margin-bottom: 15px;
    opacity: 0;
    animation: fadein 2s ease forwards;
`;


export enum Currency {
    GBP = '#11C7BD',
    USD = '#A288E3'
}

export type ToggleCurrency = () => void;
export type MapRef = L.Map | any;

 
interface SelectedCountry extends Country {
    name: string
}

export enum Sections {
    map,
    country,
    manufacturer
}

export default (({}) => {


    const {height} = useWindowSize();
   
    const [currency, setSelectedCurrency]: StateHook<Currency> = React.useState(Currency.GBP);

    const toggleCurrency: ToggleCurrency = () => setSelectedCurrency(cc => cc === Currency.GBP ? Currency.USD : Currency.GBP);



    const mapRef: MapRef = React.useRef();


  
    const handleMapZoom: HandleMapZoom = zoomModifier => {
        const currentZoom = (mapRef as any).current.leafletElement._zoom;
        const newZoom = currentZoom + zoomModifier;
        (mapRef as any).current.leafletElement.setZoom(newZoom);
    }


    const [selectedCountry, setSelectedCountry]: StateHook<string> = React.useState();

    const _selectedCountry: SelectedCountry = {
        ...countries[selectedCountry],
        name: selectedCountry
    }    

    const [activeSection, setActiveSection]: StateHook<Sections> = React.useState(Sections.map);

    const portalRef = React.useRef();

    React.useEffect(() => {
        setShowInstruction(true);
    }, []);

    React.useEffect(() => {
        window.dispatchEvent(new Event('resize'));
    }, [activeSection]);

    const [sidebarHovered, setSidebarHovered]: StateHook<number> = React.useState(null);

    const [showInstruction, setShowInstruction]: StateHook<boolean> = React.useState(false);

    const currentSection: unknown = activeSection
    
    return (
        <App screenHeight={currentSection === Sections.map ? `${height}px` : 'auto'} className="app" id="APP" currency={currency}>
            <main> 
                {/* header */}
                <Header screenHeight={height} activeSection={activeSection} setActiveSection={setActiveSection}  />
                {/* selected view */}
                {
                    currentSection === Sections.map && (
                        <>
                            <Map mapRef={mapRef} currency={currency} setSelectedCountry={setSelectedCountry} sidebarHovered={sidebarHovered} />
                            <Disclaimer>Initial data from CompaniesMarketGap.com and the International Monetary Fund.</Disclaimer>
                        </>
                    )
                }
                {
                    currentSection === Sections.country && (
                        <CountryData setSelectedCountry={setSelectedCountry} currency={currency} />
                    )
                }
                {
                    currentSection === Sections.manufacturer && (
                        <ManufacturerData currency={currency} />
                    )
                }
                
                {/* footer */}
                <Footer activeSection={activeSection} currency={currency} toggleCurrency={toggleCurrency} handleMapZoom={handleMapZoom} />
            </main>
            {
                currentSection === Sections.map && (
                    <Sidebar currency={currency} mapRef={mapRef} setSidebarHovered={setSidebarHovered} />
                )
            }
            {
                selectedCountry && portalRef.current && (
                    <Lightbox screenHeight={height}>
                        <CountryPopup currency={currency} setSelectedCountry={setSelectedCountry} country={_selectedCountry}>
                            <CurrencyToggler currency={currency} toggleCurrency={toggleCurrency} />
                        </CountryPopup>
                    </Lightbox>
                )
            }
            {
                (showInstruction as boolean) === true && portalRef.current && (
                    <Lightbox screenHeight={height}>
                        <Instruction setShowInstruction={setShowInstruction} />
                    </Lightbox>
                )
            }
            <div ref={portalRef} id="PORTAL"></div>
        </App>
    )
}) as AppContainerComponent;

interface props {
}
type AppContainerComponent = React.FC<props>;

export type EnterMap = () => void; 
export type HandleMapZoom = (zoomModifier: -1 | 1) => void;