import * as React from 'react';
import styled from 'styled-components';

import {Currency, HandleMapZoom, Sections, ToggleCurrency} from '../Containers/App';
import CurrencyToggler from './CurrencyToggler';
import MapControl from './MapControl';

// CURRENCY TOGGLER BREAKS AT 350 PX WIDTH

const Footer = styled.footer`
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    @media screen and (max-width: 500px) {
        .hideMobile {
            display: none;
        }
    }
    @media screen and (max-width: 620px) {
        .currencyToggler {
            p {
                display: none;
            }
        }
    }
`;

const Logo = styled.a`
    margin-top: auto;
    display: flex;
    img {
        margin-top: auto;
        width: 130px;
    }
`;



export default (({currency, toggleCurrency, handleMapZoom, activeSection}) => {
    return (
        <Footer>
            {/* map controls */}
            { activeSection === Sections.map && <MapControl handleMapZoom={handleMapZoom} />}
 
            {/* LOGO */}
            <Logo href="https://www.vanarama.com/" className={activeSection === Sections.map ? 'hideMobile' : ''}>
                <img src={require('../images/vanarama-logo-vector.png')} alt="" />
            </Logo>

            {/* currency toggler */}
            <div style={{marginLeft: 'auto'}}>
                <CurrencyToggler currency={currency} toggleCurrency={toggleCurrency} />
            </div>
        </Footer>
    )
}) as FooterComponent

interface props {
    currency: Currency;
    toggleCurrency: ToggleCurrency;
    handleMapZoom: HandleMapZoom;
    activeSection: Sections
};

type FooterComponent = React.FC<props>;
