export default (({mapRef, currency, setSidebarHovered}) => {
    const [sidebarOpen, setSidebarOpen]: StateHook<boolean> = React.useState(false);

    const toggleSidebar: () => void = () => {
        setSidebarOpen(cso => !cso);
        let shouldFixMap = innerWidth > 800 ? true : false;
        function fixMap() {
            mapRef.current.leafletElement.invalidateSize();
            window.dispatchEvent(new Event('resize'));
            if(shouldFixMap) {
                requestAnimationFrame(fixMap);
            }
        }
        fixMap();
        setTimeout(() => {
            shouldFixMap = false;
        }, 1200);
    };

    
    const onCompanyEnter: (company: any) => void = (company) => {
        const revenue: number = +company["Revenue ($)"].replace(/,/g, '');

        setSidebarHovered(revenue);
    }
    const onCompanyLeave: () => void = () => {

        setSidebarHovered(null);
    }

    return (
        <Sidebar sidebarOpen={sidebarOpen}>
            <SidebarToggler onClick={toggleSidebar}>Car Manufacturers 
                <svg xmlns="http://www.w3.org/2000/svg" width="9.948" height="6.389" viewBox="0 0 9.948 6.389" style={{transform: `rotate(${sidebarOpen ? 180 : 0}deg)`}}>
                    <path id="Path_744" data-name="Path 744" d="M3140.533,5110.474l4.267-4.268,4.267,4.268" transform="translate(-3139.826 -5104.792)" fill="none" stroke="#fff" strokeWidth="2"/>
                </svg>
            </SidebarToggler>
            <Instruction>Hover over the car companies below to see how their spending power fairs in comparison to each country on the world map</Instruction>
            <Legend>
                <LegendItem colour="#29DE8E">Greater Revenue</LegendItem>
                <LegendItem colour="#DA1C5C">Lower Revenue</LegendItem>
            </Legend>
            <CompanyList>
                {
                    companies.map((company: any, index: number) => (
                        <Company onMouseEnter={() => onCompanyEnter(company)} onMouseLeave={onCompanyLeave} currency={currency} key={company.Carmaker}>
                            <img src={require(`../images/logos/${company.Carmaker}.svg`)} alt="" className="logo" />
                            <div className="mid">
                                <p className="name-origin">  
                                    <span className="companyName">{company.Carmaker}</span>
                                    <span className="companyOrigin">{company.origin}</span>
                                </p>
                                <p className="companyRevenue">
                                    <span>{
                                        currency === Currency.GBP ? company['Revenue (£)'] : company['Revenue ($)']
                                    }</span>
                                </p>
                            </div>
                        </Company>
                    ))
                }
            </CompanyList>
        </Sidebar>
    )
}) as SidebarComponent

interface props {
    mapRef: MapRef,
    currency: Currency,
    setSidebarHovered: StateDispatch<number>
};
type SidebarComponent = React.FC<props>;