const getFlag = (countryName: string) => {
    try {
        return require(`../images/flags/${countryName}.svg`);
    } catch (err) {
        return 'https://i.imgur.com/NrkdG70.png';
    }
}



type SortType = 'countryName' | 'GDP' | 'Continent';

export default (({currency, setSelectedCountry}) => {


    

    

    const [pagination, setPagination]: StateHook<number> = React.useState(0);

    
    
    const {width} = useWindowSize();

    const shouldMerge = width <= 990;


    const [tableOrder, setTableOrder]: StateHook<TableOrder> = React.useState('desc');
    const [tableSort, setTableSort]: StateHook<SortType> = React.useState<SortType>('GDP');



    const countries: any[]  = Object.values(_countries).sort((a, b) => {
        if(tableSort === 'Continent' || tableSort === 'countryName') {
            if(tableOrder === 'desc') {
                return (a[tableSort] as string).localeCompare(b[tableSort])
            } else {
                return (b[tableSort] as string).localeCompare(a[tableSort])
            }
        } else {
            const GDPType = `GDP (${currency === Currency.GBP ? '£' : '$'})`;
            const GDPAsNumber: any = {
                a: (a as any)[GDPType].replace(/,/g, ''),
                b: (b as any)[GDPType].replace(/,/g, '')
            }
            if(tableOrder === 'desc') {
                return GDPAsNumber.b - GDPAsNumber.a;
            } else {
                return GDPAsNumber.a - GDPAsNumber.b;
            }
        }
    })
    

    const chunkedCountries: any[][] = chunk(countries, 20);
    const cols: any[] = chunk(chunkedCountries, 2);


    const onNextPrevPage: (page: number) => void = page => {
        if(cols[page]) {
            setPagination(page);
        }
    }


    const onHeaderClick: (sortType: SortType) => void = sortType => {

        if(tableSort === sortType) {
            setTableOrder(cto => cto === 'asc' ? 'desc' : 'asc');
        } else {
            setTableOrder('desc');
            setTableSort(sortType);
        }

    }

    return (
        <CountryData>
            <div className="tableContainer">
                {
                    (shouldMerge ? [1] : cols[pagination]).map((col: any, colIndex: number) => {
                        
                        return (
                            <Table key={`table-${colIndex}`}>
                                <TableHead tableOrder={tableOrder}>
                                    <tr>
                                        <th className={`${tableSort === 'countryName' ? 'active' : ''}`} onClick={() => onHeaderClick('countryName')}> <div>Country</div> </th>
                                        <th className={`${tableSort === 'Continent' ? 'active' : ''} continent`} onClick={() => onHeaderClick('Continent')} ><div>Continent</div></th>
                                        <th className={`${tableSort === 'GDP' ? 'active' : ''} currencyColour`} onClick={() => onHeaderClick('GDP')} ><div>GDP ({currency === Currency.GBP ? '£' : '$'})</div></th>
                                    </tr>
                                </TableHead>
                                <TableBody>
                                    <tr>
                                        <td>
                                            <div></div>
                                        </td>
                                    </tr>
                                    {
                                        (shouldMerge ? [...cols[pagination][0], ...cols[pagination][1]] : col).map((country: any, index: number) => {
                                            
                                        
                                            const flag = getFlag(country.countryName);


                                            return (
                                                <tr onClick={() => setSelectedCountry(country.countryName)} key={`tr-${country.countryName}`}>
                                                    <td>
                                                        <div>
                                                        <img src={flag} alt="" /> {country.countryName}
                                                        </div>
                                                    </td>
                                                    <td className="continent">
                                                        <div>{country.Continent}</div>
                                                    </td>
                                                    <td className="currencyColour">
                                                        <div>{currency === Currency.GBP ? '£' : '$'}{country[`GDP (${currency === Currency.GBP ? '£' : '$'})`]}</div>
                                                    </td>
                                                </tr>
                                            )
                                        })
                                    }
                                </TableBody>
                            </Table>
                        )
                    })
                }
            </div>
            <Pagination>
                <p className={'currencyColour'}>Page</p>
                <div className="buttons">

                    <button className={`arrow ${pagination > 0 ? '' : 'hidden'}`} onClick={() => onNextPrevPage(pagination - 1)}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="5.994" height="9.158" viewBox="0 0 5.994 9.158"><path d="M3140.534,5110.079l3.872-3.873,3.872,3.873" transform="translate(-5104.792 3148.985) rotate(-90)" fill="none" className={'currencyColour'} strokeWidth="2"/></svg>
                    </button>

                    {
                        cols.map((col: any, index: number) => (
                            <button key={`pagination-button-${index}`} className={`${index === pagination ? 'active' : ''} currencyColour`} onClick={() => setPagination(index)}>{index + 1}</button>
                        ))
                    }
                    <button className={`arrow ${pagination < (cols.length - 1) ? '' : 'hidden'}`} onClick={() => onNextPrevPage(pagination + 1)}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="5.994" height="9.158" viewBox="0 0 5.994 9.158"><path d="M3140.534,5110.079l3.872-3.873,3.872,3.873" transform="translate(5110.786 -3139.827) rotate(90)" fill="none" className={'currencyColour'}strokeWidth="2"/></svg>
                    </button>
                </div>
            </Pagination>
            <BackToTop />
        </CountryData>
    )
}) as CountryDataComponent;


interface props {
    currency: Currency,
    setSelectedCountry: StateDispatch<string>
}
type CountryDataComponent = React.FC<props>;


