import * as React from 'react';
import styled from 'styled-components';
import { Currency } from '../Containers/App';
import { StateDispatch, StateHook } from '../global.types';
import closeComponent from '../utils/closeComponent';
import getFlag from '../utils/getFlag';
import CountryManufacturerTable from './CountryManufacturerTable';
    // BREAKS AT 700 PX WIDTH;
const CountryPopup = styled.div`

    color: white;
    padding: 40px 50px;
    background: url('${require('../images/countryPopupBackground.svg')}');
    background-size: cover;
    background-position: center;
    width: 100%;
    max-width: 788px;
    border-radius: 9px;
    display: flex;
    flex-direction: column;
    position: relative;
    opacity: 0;
    animation: fadein 1s ease forwards;
    animation-delay: 450ms;
    header {
        display: flex;
        margin-bottom: 20px;
        .flag {
            width: 76px;
            height: 76px;
            margin-right: 20px;
        }
        .details {
            .countryName {
                font-weight: 800;
                font-size: 28px;
                line-height: 30px;
                text-transform: uppercase;
            }
            .continent {
                font-family: roc-grotesk-wide, sans-serif;
                font-weight: 500;
                text-transform: uppercase;
                font-size: 10px;
                color: #CACEE6;
                line-height: 16px;
            }
            .gdp {
                font-size: 18px;
                line-height: 20px;
                font-weight: 500;
                span {
                    font-weight: 600;

                }
            }
        }
    }
    .copy {
        font-family: roc-grotesk-wide, sans-serif;
        font-weight: 500;
        text-transform: uppercase;
        font-size: 12px;
        margin-bottom: 24px;
        line-height: 18px;
        color: #CACEE6;
    }
    .currencyToggler {
        margin-left: auto;
    }
    @media screen and (max-width: 700px) {
        padding: 24px;
        header {
            margin-bottom: 15px;
            .flag {
                display: none;
            }
            .countryName {
                font-size: 28px;
            }
        }
        .copy {
            font-size: 10px;
            line-height: 15px;
            margin-bottom: 15px;
        }
    }
    @media screen and (max-width: 370px) {
        padding: 20px 10px;
        header {
            margin-bottom: 5px;
            .countryName {
                font-size: 22px;
                line-height: 24px;
            }
        }
        .copy {
            font-size: 9px;
            line-height: 12px;
        }
    }
    @media screen and (max-height: 650px) {
        height: 100%;
    }
`;


const Closebutton = styled.button`
    width: 40px;
    height: 40px;
    background: #2A2D40;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    cursor: pointer;
    transition: 500ms;
    transform-origin: center;
    transform: translate(50%,-35%) rotate(45deg);
    position: absolute;
    top: 0;
    right: 0;
    svg {
        pointer-events: none;
        transform-origin: center;
        transform: rotate(45deg);
    }
    @media screen and (max-height: 650px) {
        transform: none;
        top: 5px;
        right: 5px;
        background: transparent;
        svg {
            transform: none;
        }
    }
`;

export default (({country, setSelectedCountry, currency, children}) => {


    const popupRef: React.MutableRefObject<HTMLDivElement> = React.useRef(null)

            
    React.useEffect(() => {
        
        const lightboxNode: ParentNode = popupRef.current.parentNode;
        const closeLightbox = (evt: MouseEvent) => {
            if((evt.target as HTMLDivElement).id === 'lightbox') {
                closeComponent(setTransitionOut, closePopup)
            }
        }

        if(lightboxNode) {
            (lightboxNode as HTMLDivElement).addEventListener('click',  closeLightbox);
        }

        return () => (lightboxNode as HTMLDivElement).removeEventListener('click', closeLightbox)
    }, []);

    const GDP: number = Number(currency === Currency.GBP
    ?  country["GDP (£)"].replace(/,/g, '')
    :  country["GDP ($)"].replace(/,/g, ''));

    const currencySymbol = currency === Currency.GBP ? '£' : '$';
    
    const [transitionOut, setTransitionOut]: StateHook<boolean> = React.useState(false);

    const closePopup = () => setSelectedCountry(null);

    const flag: string = getFlag(country.name);
    return (
        <CountryPopup ref={popupRef}>
            <Closebutton onClick={() => closeComponent(setTransitionOut, closePopup)} className="currencyBackground--hover">
                <svg xmlns="http://www.w3.org/2000/svg" width="15.589" height="15.589" viewBox="0 0 15.589 15.589"><g transform="translate(561.454 800.937) rotate(135)"><path d="M0,0V19.046" transform="translate(-178.863 952.333) rotate(-90)" fill="none" stroke="#fff" strokeWidth="3"/><path d="M0,0V19.046" transform="translate(-169.34 942.81)" fill="none" stroke="#fff" strokeWidth="3"/><path d="M0,0V19.046" transform="translate(-178.863 952.333) rotate(-90)" fill="none" stroke="#fff" strokeWidth="3"/></g></svg>

            </Closebutton>
            <header>
                <img 
                    src={flag} 
                    alt={`${country.name} flag`} 
                    className="flag" 
                />
                <div className="details">
                    <p className="continent">{country.Continent}</p>
                    <h2 className="countryName">{country.name}</h2>
                    <p className="gdp currencyColour">GDP: <span>{currencySymbol}{GDP.toLocaleString()}</span></p>
                </div>
            </header>
            <p className="copy">Shown below are the revenues of all car manufacturers in our research, alongside the difference in value compared to {country.name}’s GDP</p>
            <CountryManufacturerTable gdp={GDP} currency={currency}></CountryManufacturerTable>
            {children}
        </CountryPopup>
    )
}) as CountryPopup


interface props {
    country: any,
    setSelectedCountry: StateDispatch<string>,
    currency: Currency,
    children: React.ReactNode
}

type CountryPopup = React.FC<props>;