type SortType = 'Carmaker' | 'origin' | 'Revenue' | 'originGDP' | 'percentage';


interface CompanyExtended extends Company {
    originGDP: string,
    percentage: number
}

export default (({currency}) => {



    const [tableOrder, setTableOrder]: StateHook<TableOrder> = React.useState('desc');
    const [tableSort, setTableSort]: StateHook<SortType> = React.useState<SortType>('percentage');


    const sortedCompanies: CompanyExtended[]  = Object.values(companies).map((company: Company, index) => {
        const revenue = company[`Revenue (${currency === Currency.GBP ? '£' : '$'})`];
        const GDP = countries[company.origin][`GDP (${currency === Currency.GBP ? '£' : '$'})`];
        const revenueNum = +company[`Revenue (${currency === Currency.GBP ? '£' : '$'})`].replace(/,/g, '');
        const GDPNum = +countries[company.origin][`GDP (${currency === Currency.GBP ? '£' : '$'})`].replace(/,/g, '');

        const revenueAsPercentageOfGDP: string = ((revenueNum / GDPNum) * 100).toFixed(2);
        return Object.assign({}, company, {
            ...company,
            originGDP: GDP,
            percentage: +revenueAsPercentageOfGDP
        })
    }).sort((a, b) => {
        if(tableSort === 'Carmaker' || tableSort === 'origin') {
            if(tableOrder === 'desc') {
                return (a[tableSort] as string).localeCompare(b[tableSort])
            } else {
                return (b[tableSort] as string).localeCompare(a[tableSort])
            }
        } else if (tableSort === 'Revenue') {
            const RevenueType = `Revenue (${currency === Currency.GBP ? '£' : '$'})`;
            const RevenueAsNumber: any = {
                a: (a as any)[RevenueType].replace(/,/g, ''),
                b: (b as any)[RevenueType].replace(/,/g, '')
            }
            if(tableOrder === 'desc') {
                return RevenueAsNumber.b - RevenueAsNumber.a;
            } else {
                return RevenueAsNumber.a - RevenueAsNumber.b;
            }
        } else if (tableSort === 'percentage') {
            
            if(tableOrder === 'desc') {
                return b['percentage'] - a['percentage'];
            } else {
                return a['percentage'] - b['percentage'];
            }
        } else if (tableSort === 'originGDP') {
            
            if(tableOrder === 'desc') {
                return +b.originGDP.replace(/,/g, '') - +a.originGDP.replace(/,/g, '');
            } else {
                return +a.originGDP.replace(/,/g, '') - +b.originGDP.replace(/,/g, '');
            }
        }
    })


    const onHeaderClick: (sortType: SortType) => void = sortType => {

        if(tableSort === sortType) {
            setTableOrder(cto => cto === 'asc' ? 'desc' : 'asc');
        } else {
            setTableOrder('desc');
            setTableSort(sortType);
        }

    }

    return (
        <ManufacturerData>
            <Instruction>Below are the largest car manufacturers, with their revenues shown as a percentage of their country of origin’s GDP – demonstrating the scale of wealth in the automotive industry.</Instruction>
            <Table>
                <TableHead tableOrder={tableOrder}>
                    <tr>
                        <th className={`${tableSort === 'Carmaker' ? 'active' : ''}`} onClick={() => onHeaderClick('Carmaker')}><div><p>Manufacturer</p></div></th>
                        <th className={`currencyColour ${tableSort === 'Revenue' ? 'active' : ''}`} onClick={() => onHeaderClick('Revenue')}><div><div><p>Revenue {currency === Currency.GBP ? '£' : '$'}</p></div></div></th>
                        <th className={`${tableSort === 'origin' ? 'active' : ''}`} onClick={() => onHeaderClick('origin')}><div><p>Country <wbr /> of Origin</p></div></th>
                        <th onClick={() => onHeaderClick('originGDP')} className={`currencyColour ${tableSort === 'originGDP' ? 'active' : ''}`}><div><div><p>Country of Origin GDP {currency === Currency.GBP ? '£' : '$'}</p></div></div></th>
                        <th className={`${tableSort === 'percentage' ? 'active' : ''}`} onClick={() => onHeaderClick('percentage')}><div><p>Manufacturer <wbr /> Revenue as a % of GDP</p></div></th>
                    </tr>
                </TableHead>
                <TableBody>
                    <tr>
                        <td>
                            <div></div>
                        </td>
                    </tr>
                    {
                        sortedCompanies.map((company: Company) => {
                            const revenue = company[`Revenue (${currency === Currency.GBP ? '£' : '$'})`];
                            const GDP = countries[company.origin][`GDP (${currency === Currency.GBP ? '£' : '$'})`];
                            const revenueNum = +company[`Revenue (${currency === Currency.GBP ? '£' : '$'})`].replace(/,/g, '');
                            const GDPNum = +countries[company.origin][`GDP (${currency === Currency.GBP ? '£' : '$'})`].replace(/,/g, '');

                            const revenueAsPercentageOfGDP: string = ((revenueNum / GDPNum) * 100).toFixed(2);

                            const flag: string = getFlag(company.origin);

                            return (
                                <tr key={`manufacturer-stats-${company.Carmaker}`}>
                                    <td>
                                        <div>
                                            <img src={require(`../images/logos/${company.Carmaker}.svg`)} alt={`logo-for-${company.Carmaker}`} /> 
                                            {company.Carmaker}
                                        </div>
                                    </td>
                                    <td className="currencyColour"><div><p>{currency === Currency.GBP ? '£' : '$'}{revenue}</p></div></td>
                                    <td>
                                        <div>
                                            <img 
                                                src={flag} 
                                                alt={`${company.origin} flag`} 
                                                className="flag" 
                                            />
                                            {company.origin}
                                        </div>
                                    </td>
                                    <td className="currencyColour">
                                        <div>
                                            <p>
                                                <img 
                                                    src={flag} 
                                                    alt={`${company.origin} flag`} 
                                                    className="flag" 
                                                />{currency === Currency.GBP ? '£' : '$'}{GDP}
                                            </p>
                                        </div>
                                    </td>
                                    <td>
                                        <div>
                                            <p className="rev">{revenueAsPercentageOfGDP}%</p> 
                                            <div className="progress currencyColour">
                                                <div 
                                                    className="bar currencyBackground" 
                                                    style={{
                                                        width: `${+revenueAsPercentageOfGDP * 10}%`
                                                    }}
                                                >

                                                </div>
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </TableBody>
            </Table>
            <BackToTop />
        </ManufacturerData>
    )
}) as ManufacturerDataComponent

interface props {
    currency: Currency
}

type ManufacturerDataComponent = React.FC<props>;