import * as React from 'react';
import styled from 'styled-components';

const MobileNav = styled.nav`
    .socialShareButtons {
        justify-content: center;
    }
`;


export default (({children, toggleNavOpen}) => {

    React.useEffect(() => {
        
        const lightboxNode = document.getElementById('lightbox');
        const closeLightbox = (evt: MouseEvent) => {
            if((evt.target as HTMLDivElement).id === 'lightbox') {
                toggleNavOpen();
            }
        }

        if(lightboxNode) {
            (lightboxNode as HTMLDivElement).addEventListener('click',  closeLightbox);
        }

        return () => (lightboxNode as HTMLDivElement).removeEventListener('click', closeLightbox)
    }, []);

    return (
        <MobileNav>
            {children}
        </MobileNav>
    )
}) as MobileNavComponent


interface props {
    children: React.ReactNode,
    toggleNavOpen: () => void
};
type MobileNavComponent = React.FC<props>;